"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { getAuth } from "firebase/auth";
import { app, db } from "@/app/api/firebase";
import { collection, getDocs, query, orderBy } from "firebase/firestore";
import Sidebar from "@/app/components/sidebar";
import Navbar from "@/app/components/navbar";
import CreateMeetingModal from "@/app/components/CreateMeetingModal";

const MeetingListItem = ({ meeting, users }) => {
    const router = useRouter();


    const getStatusColor = (status) => {
        switch (status?.toLowerCase()) {
            case 'scheduled':
                return 'border-blue-400';
            case 'completed':
                return 'border-green-400';
            case 'cancelled':
                return 'border-red-400';
            default:
                return 'border-gray-300';
        }
    };

    const formatDateTime = (datetime) => {
        if (!datetime) return { date: '', time: '' };
        try {
            const date = new Date(datetime);
            return {
                date: date.toLocaleDateString('en-GB', {
                    day: '2-digit',
                    month: '2-digit',
                    year: 'numeric'
                }),
                time: date.toLocaleTimeString('en-GB', {
                    hour: '2-digit',
                    minute: '2-digit'
                })
            };
        } catch (error) {
            return { date: '', time: '' };
        }
    };

    const { date, time } = formatDateTime(meeting.datetime);

    const organizations = {}; // Assuming you have a way to fetch organizations

    const getOrganizationName = (orgId) => organizations[orgId] || 'Unknown';

    return (
        <div className={`bg-white rounded-lg border-2 ${getStatusColor(meeting.status)} mb-4`}>
            <div className="p-6">
                <div className="flex justify-between items-start">
                    <div>
                        <h3 className="text-xl font-semibold">{meeting.title}</h3>
                        <div className="text-sm text-gray-600 mt-1 space-y-1">
                            <div className="flex items-center gap-2">
                                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" 
                                        d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                                </svg>
                                <span>Organizer: {users[meeting.owner] || 'Unknown'}</span>
                            </div>
                            <p>{date}</p>
                            <p className="font-medium text-blue-600">{time}</p>
                            {meeting.location && (
                                <p className="flex items-center gap-1">
                                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" 
                                            d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" 
                                            d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                                    </svg>
                                    {meeting.location}
                                </p>
                            )}
                            <p>Organization: {getOrganizationName(meeting.organizationId)}</p>
                        </div>
                    </div>
                    <div className="text-right">
                        <span className={`inline-flex px-3 py-1 rounded-full text-sm font-medium 
                            ${meeting.status === 'scheduled' ? 'bg-blue-100 text-blue-800' :
                            meeting.status === 'completed' ? 'bg-green-100 text-green-800' :
                            meeting.status === 'cancelled' ? 'bg-red-100 text-red-800' :
                            'bg-gray-100 text-gray-800'}`}>
                            {meeting.status || 'No Status'}
                        </span>
                    </div>
                </div>
                
                {meeting.participants && meeting.participants.length > 0 && (
                    <div className="mt-4">
                        <h4 className="text-sm font-medium text-gray-700 mb-2">Participants:</h4>
                        <div className="flex flex-wrap gap-2">
                            {meeting.participants?.map((participant, index) => (
                                <span key={index} 
                                    className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-800">
                                    {users[participant] || 'Unknown'}
                                </span>
                            ))}
                        </div>
                    </div>
                )}

                {meeting.notes && (
                    <div className="mt-3 text-sm text-gray-600">
                        <p className="line-clamp-2">{meeting.notes}</p>
                    </div>
                )}

                <div className="flex justify-end items-center mt-4">
                    <button 
                        onClick={() => router.push(`/meetings/${meeting.id}`)}
                        className="text-blue-600 hover:text-blue-800 transition-colors"
                    >
                        view details &gt;&gt;
                    </button>
                </div>
            </div>
        </div>
    );
};

export default function Meetings() {
    const [meetings, setMeetings] = useState([]);
    const [loading, setLoading] = useState(true);
    const [isModalOpen, setIsModalOpen] = useState(false);

    const [users, setUsers] = useState({});

    const fetchUsers = async () => {
        try {
            const usersSnapshot = await getDocs(collection(db, 'users'));
            const usersMap = {};
            usersSnapshot.docs.forEach(doc => {
                usersMap[doc.id] = doc.data().name;
            });
            setUsers(usersMap);
        } catch (error) {
            console.error('Error fetching users:', error);
        }
    };

    const fetchMeetings = async () => {
        try {
            const meetingsQuery = query(
                collection(db, 'meetings'),
                orderBy('datetime', 'desc')
            );
            const querySnapshot = await getDocs(meetingsQuery);
            const meetingsList = querySnapshot.docs.map(doc => ({
                id: doc.id,
                ...doc.data()
            }));
            setMeetings(meetingsList);
        } catch (error) {
            console.error('Error fetching meetings:', error);
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchUsers();
        fetchMeetings();
    }, []);

    return (
        <div className="flex h-screen bg-gray-50">
            <Sidebar />
            <div className="flex-1">
                <Navbar />
                <div className="p-8">
                    <div className="flex justify-between items-center mb-6">
                        <h1 className="text-2xl font-bold">Meetings</h1>
                        <button
                            onClick={() => setIsModalOpen(true)}
                            className="px-4 py-2 bg-blue-600 text-white text-sm font-medium rounded-md hover:bg-blue-700 
                                focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 flex items-center gap-2"
                        >
                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 4v16m8-8H4" />
                            </svg>
                            New Meeting
                        </button>
                    </div>
                    {loading ? (
                        <div className="text-center">Loading meetings...</div>
                    ) : meetings.length === 0 ? (
                        <div className="text-center text-gray-500">No meetings found</div>
                    ) : (
                        <div className="max-w-3xl">
                            {meetings.map((meeting) => (
                                <MeetingListItem 
                                    key={meeting.id} 
                                    meeting={meeting}
                                    users={users}
                                />
                            ))}
                        </div>
                    )}
                </div>
                <CreateMeetingModal 
                    isOpen={isModalOpen}
                    onClose={() => setIsModalOpen(false)}
                    onSuccess={fetchMeetings}

                />
            </div>
        </div>
    );
}
